*{
	padding: 0;
	margin: 0;
}
body{
	font-family: Galeria Bold;
	background-color: #f4f4f4;
}
.logo{
	font-family: Galeria light;
	transition: .1s ease;
	font-size: 30px;
	margin-top: 16px;
	margin-left: 2%;
}
#logoMobile{
	text-align: end;
}
.navbar{
	border-bottom: 6px solid #1c1c1c;
	overflow: hidden;
	min-height: 60px;
	display: block;
}
.navbar a:hover{
	color: red;
}
.side-nav{
	height: 100%;
	position: fixed;
	top: 0;
	right: -1px;
	background-color: #5987aa;
	transition: .1s;
}
#open-side{
	width: 60px;
	margin: 0px;
	z-index: 200;
	background: transparent;
	border: 0px solid;
}
#titulos{
	font-size: 90px;
	display: block;
	transition: .1s;
	text-align: right;
	list-style: none;
}
.windows-nav{
	position: absolute;
	z-index: 0;

}
.side-nav span{
	position: relative;
}
.side-nav span button {
	position: absolute;
	font-size: 50px;
}
.ventana , .ventana2 {
	color: blue;
	width: 420px;
	height: 450px;
	position: absolute;
}
.ventana{
	background: green;
}
.ventana2{
	z-index: 100;
	border: 6px solid;
	height: 400px;
	top: 20px;
	left: 30px;
}

#side-menu{
	width:15%;
}
#titulos a{
	text-decoration: none;
}
#side-menu.active {
	width:80%;
	transition: .1s;
}